
import { defineComponent, PropType, computed } from 'vue'
// import { ColumnProps } from '../store'
import { ColumnProps } from '../stores/colums'
export default defineComponent({
  name: 'ColumnList',
  props: {
    list: {
      type: Array as PropType<ColumnProps[]>, // 想要把一个构造函数断言成一个类型, 需要使用到 PropType 这个方法
      required: true
    }
  },
  setup (props) {
    const columnList = computed(() => {
      return props.list.map((column) => {
        if (!column.avatar?._id) {
          column.avatar = {
            url: require('@/assets/column.jpg')
          }
        } else {
          if (
            !column.avatar.url?.includes('?x-oss-process=image/resize,m_fixed')
          ) {
            column.avatar.url =
              column.avatar.url +
              '?x-oss-process=image/resize,m_fixed,h_80,w_80' // 将图片强制缩放到 50px * 50px
          }
        }
        return column
      })
    })
    return {
      columnList
    }
  }
})
