
import ValidataForm from '../components/ValidataForm.vue'
import ValidateInput, { RulesProp } from '../components/ValidateInput.vue'
import createMessage from '../components/createMessage'
import { defineComponent, reactive } from 'vue'
import { useRouter } from 'vue-router'
import { useUserStore } from '../stores/user'
export interface formDataProps {
  email: string
  nickName: string
  password: string
  repeatPassword: string
}
export default defineComponent({
  setup () {
    const store = useUserStore()
    const formData = reactive<formDataProps>({
      email: '',
      nickName: '',
      password: '',
      repeatPassword: ''
    })
    const router = useRouter()
    const emailRules: RulesProp = [
      { type: 'required', message: '邮箱地址不能为空' },
      { type: 'email', message: '请输入正确的邮箱地址格式' }
    ]
    const nickNameRules: RulesProp = [
      { type: 'required', message: '昵称不能为空' }
    ]
    const passwordRules: RulesProp = [
      { type: 'required', message: '密码不能为空' },
      {
        type: 'password',
        message: '密码长度在6-15位之间,且只能是数字和字母的组合'
      }
    ]
    const repeatPasswordRules: RulesProp = [
      { type: 'required', message: '请再次输入密码' },
      {
        type: 'custom',
        message: '两次输入的密码不相同',
        validator: () => {
          return formData.password === formData.repeatPassword
        }
      }
    ]
    const onFormSubmit = (result: boolean) => {
      if (result) {
        const payload = {
          email: formData.email,
          password: formData.password,
          nickName: formData.nickName
        }
        store.signUp(payload)
        createMessage('注册成功！正在跳转登陆页面', 'success')
        setTimeout(() => {
          router.push('/login')
        }, 2000)
      }
    }
    return {
      onFormSubmit,
      formData,
      emailRules,
      nickNameRules,
      passwordRules,
      repeatPasswordRules
    }
  },
  components: {
    ValidataForm,
    ValidateInput
  }
})
