
import { defineComponent, ref } from 'vue'
import ValidateInput, { RulesProp } from '../components/ValidateInput.vue'
import ValidateForm from '../components/ValidataForm.vue'
import creatrMessage from '../components/createMessage'
import { useRouter } from 'vue-router'
// import { useStore } from 'vuex'
// import { GlobalDataProps } from '@/store'
import { useUserStore } from '../stores/user'

export default defineComponent({
  name: 'LoginView',
  setup () {
    // const store = useStore<GlobalDataProps>()
    const store = useUserStore()
    const router = useRouter()
    const emailVal = ref('')
    const passwordVal = ref('')
    const emailRules: RulesProp = [
      { type: 'required', message: '邮箱地址不能为空' },
      { type: 'email', message: '请输入正确的邮箱地址格式' }
    ]
    const passwordRules: RulesProp = [
      { type: 'required', message: '密码不能为空' },
      {
        type: 'password',
        message: '密码长度在6-15位之间'
      }
    ]
    const onFormSubmit = (result: boolean) => {
      if (result) {
        const payload = {
          email: emailVal.value,
          password: passwordVal.value
        }
        store.loginAndFetch(payload)
          .then(() => {
            creatrMessage('登陆成功！2秒后跳转首页', 'success')
            setTimeout(() => {
              router.push('/')
            }, 2000)
          })
      }
    }
    return {
      emailRules,
      emailVal,
      passwordRules,
      passwordVal,
      onFormSubmit
    }
  },
  components: {
    ValidateInput,
    ValidateForm
  }
})
